import style from './Style.module.css'
import React,{useState,useEffect} from "react";
const Alert = ({type,msg,removeAlert,list}) => {
  useEffect(() => {
      const timeout = setTimeout(() => {
          removeAlert();
      },3000);
      return () => clearTimeout(timeout)
  },[list]);
  return <p className={`alert alert-${type}`}>{msg}</p>
}
const List = ({items,removeItem,editItem}) => {
    return(
        <div className={style.ListaContainer}>
            {items.map((item) => {
                const {id,title,image} = item;
                return(
                    <div key={id} className={style.ListaCOI}>
                            <h1>{title}</h1>
                            <img id="ImageError" src={image} alt="" />
                                <button className={style.ButtonLis} onClick={() => editItem(id)}>
                                    Editar
                                </button>
                                <button className={style.ButtonLis} onClick={() => removeItem(id)}>
                                    Remover
                                </button>
                    </div>
                )
            })}
        </div>
    )
}

const getLocalStorage = () => {
  let list = localStorage.getItem("list");
  if(list){
    return (list = JSON.parse(localStorage.getItem("list")));
  } else{
    return [];
  }
}
const Mural = () =>{
  const [name,setName] = useState("");
  const [image,setImage] = useState("");
  const [list,setList] = useState(getLocalStorage());
  const [alert,setAlert] = useState({show:false,msg:"",type:""});
  const [editId,setEditID] = useState(null);
  const [isEditing,setIdEditing] = useState(false);
  useEffect(() => {
    localStorage.setItem("list",JSON.stringify(list));
  },[list]);
  const handleSubmit = (e) => {
    e.preventDefault();
    if(!name,!image){
      ShowAlert(true,"danger","Please Enter Value")
    }else if(name && isEditing,image && isEditing){
      setList(
        list.map((item) => {
          if(item.id === editId){
            return {...item,title:name,image:image}
          }
          return item
        })
      );
      setName("");
      setImage("");
      setEditID(null);
      setIdEditing(false);
      ShowAlert(true,"Sucesso","Editao renovado")
    }else{
      ShowAlert(true,"Sucesso")
      const newItem = {id: new Date().getTime().toString(),title:name,image:image};
      setList([...list,newItem]);
      setName("");
      setImage("");
    }
  };
  const ShowAlert = (show = false, type = "" , msg ="") => {
    setAlert({show,type,msg})
  };
  const removeItem = (id) => {
    ShowAlert(true,"danger","Item Remove")
    setList(list.filter((item) => item.id !== id));
  };
  const EditItem = (id) => {
    const EditItem = list.find((item) => item.id === id);
    setIdEditing(true);
    setEditID(id);
    setName(EditItem.title);
    setImage(EditItem.image);
  };
  return(
    <div>
      <section>
        <form onSubmit={handleSubmit} >
          {alert.show && <Alert {...alert} removeAlert={ShowAlert} list={list}/>}
          <div className={style.LISTM}>
            <h1>Adicionar uma Imagem novo</h1>
            <input type="text" placeholder="Nome da Imagem" onChange={(e) => setName(e.target.value)} value={name} />
            <input type="text" placeholder="Coloque a url da Imagem" onChange={(e) => setImage(e.target.value)} value={image} />
            <button type="submit">
              {isEditing ? "Editar" : "Enviar"}
            </button>
          </div>
          <br />
        </form>
        {list.length > 0 && (
          <div>
            <List items={list} removeItem={removeItem} editItem={EditItem}/>
            <div>
            </div>
          </div>
        )}
      </section>
    </div>
  )
}
export default Mural
