import style from './Style.module.css'
function ClickPo(){
    const fetchDigimon = () => {
        var InN = document.getElementById("InputNumber").value
        const getDigimonUrl = id => `https://digimon-api.com/api/v1/digimon/${id}`
        const DigimonPromises = []
        for (let i=1;i<=InN ;i++){
            DigimonPromises.push(fetch(getDigimonUrl(i)).then(response => response.json()))
        }
        Promise.all(DigimonPromises)
        .then(digimons => {
        const lisDigimons = digimons.reduce((accumulator,digimon) => {
            accumulator =
            `
            <img id="DigiImg" src="https://digimon-api.com/images/digimon/w/${digimon.name.replace(/ /g, '_')}.png" />
          <li id="DigimonName">N${digimon.id}</id>
          <li id="DigimonName">${digimon.name}</li>
          <li id="DigimonName">Anti Corpo X:${digimon.xAntibody}</li>
          <li id="DigimonName">Data do Digimon: ${digimon.releaseDate}</li>
            `
            return accumulator
        },'')
        const ul = document.querySelector('[data-js="digimon"]')
        ul.innerHTML = lisDigimons
        })
        }
        fetchDigimon()
    }

export default function Digipedia(){
    return(
        <div className={style.DigiBody}>
             <input type="number" id="InputNumber"/>
            <ul className={style.DigiBodyX} data-js="digimon"></ul>
            <button className={style.ClickPo} onClick={ClickPo}>Pesquisar Digimon</button>
        </div>
        )
}
