import style from './Style.module.css'
function CoresGerar(tamanho){
    var primeiro = "#"
    var caracter = "0123456789abcdef"
for(var i = 0;i < tamanho;i++){
    primeiro += caracter.charAt(Math.floor(Math.random() * caracter.length))
}
return primeiro
}
var array = [];
const Salvar = () => {
    if (localStorage.Nota){
    array = JSON.parse(localStorage.getItem('Nota'));
    }
    let NewArray = document.getElementById("Texto").value = CoresGerar(6);
    array.push(NewArray);
    localStorage.Nota = JSON.stringify(array)
    let Corpo = document.getElementById("Coisa")
    Corpo.innerHTML = ""
    for(var i in array){
        let p = document.createElement("h1");
        p.innerHTML = array[i];
        p.id = "ButtonCor"
        Corpo.append(p)
        p.style.background = array[i]
    }
}
var Corpo = document.getElementById("Coisa")
for(var i in array){
    let p = document.createElement("h1");
    p.innerHTML = array[i];
    Corpo.append(p)
}
const Limpar = () =>{
    localStorage.clear()
}
export default function Cores(){
    return(
        <div className={style.Cores}>
            <input type="text" name="texto" id="Texto" className={style.TextInput}/>
                <button id="Salvar" onClick={Salvar}>Adicionar uma Cor</button>
                <button onClick={Limpar}>Limpar</button>
                <div id="Coisa"></div>
        </div>
        )
}
