import React,{useState} from "react";
import emailjs from 'emailjs-com'
import style from './Style.module.css'
import QRCode from "react-qr-code"
function EnviarEmail(e){
    e.preventDefault();

    emailjs.sendForm('service_l0hxktd', 'template_uxizrxe', e.target, 'OQ16vIH0bRwM_vEya')
      .then((result) => {
          alert("Messagem enviada com sucesso")
      }, (error) => {
          console.log(error.text);
      });
      e.target.reset();
}
function Start(){
    const today = new Date();
    var segundo = today.getSeconds();
    var minutos = today.getMinutes();
    var horas = today.getHours();
    segundo = checkTime(segundo)
    minutos = checkTime(minutos)
    document.getElementById("hora").innerHTML =  horas + ":" + minutos + ":" + segundo
    setTimeout(Start,1000);
    }
    function checkTime(i){
        if (i< 10) {i = "0" + i};
        return i;
    }
export default function TQE(){
    const [link,setLink] = useState("")
    function handleQrcode(e){
        setLink(e.target.value)
    }
    return(
        <div>
            <button onClick={Start} className={style.Comear} id="Comear">Comear</button>
            <h1 className={style.Hora} id="hora"></h1>
            <form className={style.Form} onSubmit={EnviarEmail}>
                <div>
                    <label>Nome</label>
                    <input type="text" autoFocus required placeholder="Nome" name="name" />
                </div>
                <div>
                    <label>Email</label>
                    <input type="text" required placeholder="Email" name="email"/>
                </div>
                <div>
                    <label>Message</label>
                    <textarea type="text" required placeholder="message" name="message"/>
                </div>
                <div>
                <button className={style.ButtonEmail} type="submit">Enviar Message</button>
                </div>
            </form>
            <div className={style.QrCodeBody}>
            <QRCode size="75vh" id="QrCode" className={style.Qrcode} value={link}/>
            <input type="text" placeholder='QrCode Texto' id="TextQR" onChange={(e) => handleQrcode(e)} value={link}/>
        </div>
        </div>
        )
}
