import {Link} from 'react-router-dom'
import style from './Style.module.css'
function Entrar(){
    document.getElementById("Sidebar").style.display = "block"
}
function Sair(){
    document.getElementById("Sidebar").style.display = "none"
}
function toggleDarkMode() {
    document.body.classList.toggle("dark");
  }
  function loadTheme() {
    const darkMode = localStorage.getItem("dark");
    if (darkMode) {
      toggleDarkMode();
    }
  }
  loadTheme();
  function Charge() {
    toggleDarkMode();
    localStorage.removeItem("dark");
    if (document.body.classList.contains("dark")) {
      localStorage.setItem("dark", 1);
    }
  };
export default function Header(){
  const username = localStorage.getItem('@welcome-app/username');
  const image = localStorage.getItem('@welcome-app/image');
    return(
        <div>
            <div className={style.Header}>
            <input type="checkbox" name="change-theme" id="change-theme" />
        <label for="change-theme" onClick={Charge} className={style.Theme}>
          <button class="bi bi-moon"><img src="https://static.thenounproject.com/png/673911-200.png" alt="" /></button>

          </label>
                <h1 className={style.HeaderText}><button id="Entrar" className={style.Entrar} onClick={Entrar}>  <img src="https://sumareesportes.com.br/wp-content/uploads/2022/11/menu-aberto-1.png" alt="" /></button> <span>Defenestra</span></h1>
            </div>
        <div className={style.Sidebar} id="Sidebar">
            <button className={style.Sair} onClick={Sair}>
              <img src="https://sumareesportes.com.br/wp-content/uploads/2022/11/menu-aberto-1.png" alt="" />
            </button>
            <img src={image} alt="" />
          <h1>{username}</h1>
            <Link to="/">Home</Link>
            <Link to="/Lista">Lista</Link>
            <Link to="/Cores">Cores</Link>
            <Link to="/Mural">Mural</Link>
            <Link to="/Register">Register</Link>
            <Link to="/TQE">TQE</Link>
            <Link to="/ImgPDF">PDF imagem</Link>
            <Link to="/Animation">Animation</Link>
            <Link to="/Pokedex">Pokedex</Link>
            <Link to="/Digipedia">Digipdia</Link>
        </div>
        </div>
        )
}
