import jsPDF from 'jspdf';
import style from '../Style.module.css'
import { React, useState } from 'react';

const Pdfimg = () => {
  const [photo, setPhoto] = useState(null);

  const onChangephoto = (e) => {
    setPhoto(e.target.files[0]);
  };

  console.log(photo);

  const pdfGenerate = () => {
    var doc = new jsPDF('p', 'pt', 'a4');
    var width = doc.internal.pageSize.getWidth();
    var height = doc.internal.pageSize.getHeight();
    var img = URL.createObjectURL(photo);
    doc.addImage(img, null, 0, 0, width, height, null, 'FAST');
    doc.save(`${photo.name}.pdf`);
  };

  return (
    <div>
      <div className="row mt-5">
        <div className="col-lg-3">
          {photo && (
            <div className={style.IMGPDF}>
              <img
                alt="not fount"
                width="250"
                height="250"
                className="rounded-circle shadow image-fit"
                src={URL.createObjectURL(photo)}
              />
              <br />
            </div>
          )}
        </div>
        <div className="col-lg-4">
          {' '}
          <div className={style.FormControl}>
            <input
              type="file"
              name="photo"
              onChange={onChangephoto}
              accept="image/png, image/png, image/jpeg, image/jpg"
            />
          </div>
        </div>
        <div className="col-lg">
          {' '}
          <button
            className={style.ButtnGenerator}
            onClick={pdfGenerate}
            disabled={!photo}
          >
            Download pdf
          </button>
        </div>
      </div>
    </div>
  );
};

export default Pdfimg;
