import style from './Style.module.css'
function ClickPo(){
    const fetchPokemon = () => {
        var InN = document.getElementById("InputNumber").value
        const getPokemonUrl = id => `https://pokeapi.co/api/v2/pokemon/${id}`
        const PokemonPromises = []
        for (let i=1;i<=InN ;i++){
            PokemonPromises.push(fetch(getPokemonUrl(i)).then(response => response.json()))
        }
        Promise.all(PokemonPromises)
        .then(pokemons => {
        const lisPokemons = pokemons.reduce((accumulator,pokemon) => {
            const types = pokemon.types.map(typeInfo => typeInfo.type.name)
            accumulator =
            `
            <div>
            <img id="imagePokemon"
            src="https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${pokemon.id}.png" />
            <li id="NomePoke"><h1>N${pokemon.id}</h1></id>
            <li id="NomePoke"><h2>${pokemon.name}</h2></li>
            <li id="NomePoke"><h3>${types.join(' / ')}</h3></li>
            </div>
            `
            return accumulator
        },'')
        const ul = document.querySelector('[data-js="pokemon"]')
        ul.innerHTML = lisPokemons
        })
        }
        fetchPokemon()
    }

export default function pokedex(){
    return(
        <div className={style.Pokedex}>
             <input type="number" className={style.INputDex} id="InputNumber"/>
            <ul data-js="pokemon" class="Container"></ul>
            <button className={style.PQ} onClick={ClickPo}>Pesquisar Pokemon</button>
        </div>
        )
}
