import style from './Style.module.css'
import React,{useState,useEffect} from "react";
const Alert = ({type,msg,removeAlert,listC}) => {
  useEffect(() => {
      const timeout = setTimeout(() => {
          removeAlert();
      },3000);
      return () => clearTimeout(timeout)
  },[listC]);
  return <p className={`alert alert-${type}`}>{msg}</p>
}
const List = ({items,removeItem,editItem}) => {
    return(
        <div className={style.ListaContainer}>
            {items.map((item) => {
                const {id,ListaN} = item;
                return(
                    <div className={style.ListaCO} key={id}>
                            <h1>{ListaN}</h1>
                                <button className={style.ButtonLis} onClick={() => editItem(id)}>
                                    Editar
                                </button>
                                <button className={style.ButtonLis} onClick={() => removeItem(id)}>
                                    Remover
                                </button>
                    </div>
                )
            })}
        </div>
    )
}
const getLocalStorage = () => {
  let listC = localStorage.getItem("listC");
  if(listC){
    return (listC = JSON.parse(localStorage.getItem("listC")));
  } else{
    return [];
  }
}
const App = () =>{
  const [nome,setNome] = useState("");
  const [listC,setlistC] = useState(getLocalStorage());
  const [alert,setAlert] = useState({show:false,msg:"",type:""});
  const [editId,setEditID] = useState(null);
  const [isEditing,setIdEditing] = useState(false);
  useEffect(() => {
    localStorage.setItem("listC",JSON.stringify(listC));
  },[listC]);
  const handleSubmit = (e) => {
    e.preventDefault();
    if(!nome){
      ShowAlert(true,"danger","Please Enter Value")
    }else if(nome && isEditing){
      setlistC(
        listC.map((item) => {
          if(item.id === editId){
            return {...item,ListaN:nome}
          }
          return item
        })
      );
      setNome("");
      setEditID(null);
      setIdEditing(false);
      ShowAlert(true,"Sucesso","")
    }else{
      ShowAlert(true,"Sucesso")
      const newItem = {id: new Date().getTime().toString(),ListaN:nome};
      setlistC([...listC,newItem]);
      setNome("");
    }
  };
  const ShowAlert = (show = false, type = "" , msg ="") => {
    setAlert({show,type,msg})
  };
  const removeItem = (id) => {
    setlistC(listC.filter((item) => item.id !== id));
  };
  const EditItem = (id) => {
    const EditItem = listC.find((item) => item.id === id);
    setIdEditing(true);
    setEditID(id);
    setNome(EditItem.ListaN);
  };
  return(
    <div>
      <section>
        <form onSubmit={handleSubmit}>
          {alert.show && <Alert {...alert} removeAlert={ShowAlert} list={listC}/>}
          <div className={style.LIST}>
            <h1>Adicionar uma Nota novo</h1>
            <textarea type="text" placeholder="Nome do Prato" onChange={(e) => setNome(e.target.value)} value={nome} ></textarea>
            <button type="submit">
              {isEditing ? "Editar" : "Enviar"}
            </button>
          </div>
          <br />
        </form>
        {listC.length > 0 && (
          <div>
            <List items={listC} removeItem={removeItem} editItem={EditItem}/>
          </div>
        )}
      </section>
    </div>
  )
}
export default App

